html {
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.4rem;
  background-color: #F5F5F7;
  color: #262626;
}

.big-container {
  max-width: 1718px;
  margin: 0 auto;
  padding: 0px 15px;
}

h1 {
  margin: 0px;
}

h2, .h2 {
  color: #262626;
  font-family: inherit;
  font-size: 3rem;
  font-style: normal;
  font-weight: 800;
  line-height: 135%;
  /* 54px */
  text-transform: uppercase;
  margin: 0px;
}

ul {
  padding-left: 0px;
  list-style: none;
}

a {
  text-decoration: none;
  transition: all ease 0.3s;
}

.bold-gray-text {
  color: #6A6A6A;
  text-align: center;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: 180.9%;
  /* 25.326px */
}

.button {
  text-align: center;
  font-family: inherit;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 600;
  line-height: 135%;
  /* 21.6px */
  padding: 16px 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 1px solid #3A55C1;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
}
.button-empty {
  border: 1px solid #5475F8;
  color: #5475F8;
}
.button-empty:hover {
  border: 1px solid #095CD9;
  background: #095CD9;
  color: #fff;
}

@media screen and (min-width: 1120px) {
  .bottom-part {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 70px;
  }

  .bottom-part .h2, .bottom-part .bold-gray-text {
    margin-bottom: 0px;
  }

  .bottom-part .bold-gray-text {
    max-width: 40%;
    text-align: left;
  }

  .bottom-part .button {
    margin: 0px;
  }
}
@media screen and (min-width: 1200px) {
  .big-container {
    max-width: 1318px;
  }
}
@media screen and (min-width: 1600px) {
  .big-container {
    max-width: 1718px;
  }
}
.header {
  padding: 20px 15px;
  background-color: #f5f5f7;
}
.header .logo-part {
  width: 112px;
  display: block;
}
.header .logo-part img {
  width: 100%;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  background-color: #f5f5f7;
  left: 0px;
  width: 100%;
  transform: translateX(-100%);
  transition: all ease-in-out 0.3s;
}
.nav-ul {
  list-style: none;
  padding: 15px 15px;
}
.nav-li {
  margin-bottom: 10px;
}
.nav-link {
  font-family: inherit;
  font-size: 1.6rem;
  color: #262626;
  text-align: center;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-decoration: none;
}
.nav-link:hover {
  color: #3A55C1;
}
.nav-link.active {
  color: #3A55C1;
}

.nav.active {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0%);
}

.menu-lines {
  cursor: pointer;
}

.other-info {
  display: flex;
}

.phone-button {
  margin-right: 40px;
  margin-top: 1px;
}

.dropdown {
  position: relative;
  display: inline-block;
  width: 100%;
}
.dropdown .dropdown-link {
  padding-right: 20px;
}
.dropdown .dropdown-link:after {
  content: "";
  display: block;
  width: 15px;
  height: 7px;
  position: absolute;
  right: auto;
  left: 130px;
  top: 5px;
  background-image: url("../img/icons/arrdown.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.phone-button span {
  display: none;
}

.dropdown-content {
  display: none;
  min-width: 160px;
  padding-left: 0px;
  z-index: 8;
  list-style: none;
}
.dropdown-content .nav-link {
  font-size: 1.4rem;
}

.dropdown:hover .nav-link:first-child:after svg {
  fill: #3A55C1;
}

.dropdown:hover .dropdown-content {
  display: block;
  z-index: 9;
}

@media screen and (min-width: 1120px) {
  .header .logo-part {
    display: block;
    margin-right: 47px;
    min-width: 129px;
    width: 133px;
  }

  .nav {
    opacity: 1;
    pointer-events: all;
    transform: translateX(0%);
    position: relative;
  }

  .menu-logo {
    display: flex;
    align-items: center;
    width: 80%;
  }

  .nav-ul {
    display: flex;
    justify-content: flex-start;
    flex-flow: wrap;
    align-items: center;
    padding: 14px 15px;
  }

  .dropdown {
    width: auto;
    background-color: #F5F5F7;
  }

  .dropdown .dropdown-link {
    padding-right: 14px;
  }

  .dropdown-content {
    background-color: #F5F5F7;
  }

  .dropdown:hover .dropdown-content,
.dropdown.active .dropdown-content {
    position: absolute;
    top: 1.8rem;
    z-index: 999;
    left: 0px;
  }
  .dropdown:hover .dropdown-content .nav-li,
.dropdown.active .dropdown-content .nav-li {
    padding: 16px 38px;
  }
  .dropdown:hover .dropdown-content .nav-li:hover,
.dropdown.active .dropdown-content .nav-li:hover {
    background-color: #e7e7e7;
    color: #3A55C1;
  }
  .dropdown:hover .dropdown-content .nav-link,
.dropdown.active .dropdown-content .nav-link {
    font-size: 1.4rem;
  }

  .nav-link {
    margin-right: 22px;
  }

  .nav-li {
    margin-bottom: 0px;
  }

  .menu-lines {
    display: none;
  }

  .header {
    padding: 34px 15px 31px;
  }

  .phone-button {
    border: 2px solid #111;
    display: flex;
    align-items: center;
    padding: 9px 15px;
    text-decoration: none;
    margin-right: 0px;
  }
  .phone-button span {
    display: block;
    color: #111;
    font-family: inherit;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-decoration: underline;
  }
  .phone-button .phone-icon {
    margin-right: 24px;
  }
  .phone-button:hover {
    cursor: pointer;
  }

  .dropdown .dropdown-link:after {
    right: 11px;
    top: 7px;
    left: auto;
  }
}
@media screen and (min-width: 1400px) {
  .header {
    padding: 33px 15px 32px 15px;
  }

  .nav-link {
    margin-right: 38px;
  }

  .dropdown .dropdown-link {
    padding-right: 34px;
  }

  .dropdown .dropdown-link:after {
    left: auto;
    right: 47px;
    top: 6px;
  }

  .header .logo-part {
    margin-right: 83px;
    min-width: 159px;
    width: 170px;
  }

  .phone-button {
    align-items: center;
    border: 2px solid #111;
    display: flex;
    margin-right: 0;
    padding: 16px 36px;
  }
  .phone-button span {
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 1600px) {
  .nav-link {
    margin-right: 50px;
  }
}
.main-content {
  background-image: url("../img/illustrations/main-min.png");
  background-size: cover;
  padding-left: 26px;
  padding-top: 92px;
  padding-bottom: 100px;
  position: relative;
  z-index: 5;
}

.h1-mega {
  color: #FFF;
  font-family: inherit;
  font-size: 3rem;
  font-style: normal;
  font-weight: 800;
  line-height: 116.5%;
  /* 34.95px */
  text-transform: uppercase;
  max-width: 90%;
  margin-bottom: 10px;
}

.main-subheader {
  color: #FFF;
  font-family: inherit;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 700;
  line-height: 135%;
  /* 24.3px */
  margin-bottom: 20px;
}

.micro-subheader {
  color: #FFF;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 600;
  line-height: 135%;
  /* 18.9px */
  margin-bottom: 80px;
}

.main-links {
  color: #FFF;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: 135%;
  /* 18.9px */
  margin-bottom: 24px;
  text-decoration: none;
  position: relative;
  width: fit-content;
}
.main-links:after {
  content: "";
  width: 7px;
  height: 10px;
  position: absolute;
  background: url("../img/icons/arr-right.svg");
  background-size: contain;
  background-repeat: no-repeat;
  right: -25px;
  top: 5px;
  transition: all ease 0.3s;
}
.main-links:hover:after {
  right: -40px;
}
.main-links-wrap {
  display: flex;
  flex-direction: column;
}

.social-media-block, .langs-block, .mail-link {
  display: none;
}

.bottom-part {
  background-color: #fff;
  padding: 50px 0px;
}
.bottom-part .h2 {
  text-align: center;
  margin-bottom: 20px;
}
.bottom-part .bold-gray-text {
  max-width: 90%;
  margin: 0 auto 36px;
}
.bottom-part .button {
  margin: 0 auto 50px;
}

@media screen and (min-width: 760px) {
  .main-content {
    background-image: url(../img/illustrations/main.png);
    padding-left: 85px;
    padding-top: 130px;
  }

  .h1-mega {
    font-size: 40px;
  }
}
@media screen and (min-width: 1400px) {
  .main-content {
    padding-left: 257px;
    padding-top: 133px;
  }

  .h1-mega {
    font-size: 70px;
    margin-bottom: 40px;
  }

  .main-subheader {
    font-size: 2.2rem;
  }

  .micro-subheader {
    font-size: 1.8rem;
    max-width: 400px;
    font-weight: 400;
  }

  .main-links-wrap {
    flex-direction: row;
  }

  .main-links {
    margin-right: 75px;
  }

  .social-media-block {
    display: block;
    position: absolute;
    left: -62px;
    top: -15px;
  }

  .social-media-li {
    margin-bottom: 22px;
  }
  .social-media-link {
    width: 24px;
    height: 24px;
    display: block;
  }
  .social-media-link img {
    width: 100%;
    transition: all ease 0.3s;
  }
  .social-media-link:hover img {
    opacity: 0.7;
  }

  .langs-block {
    display: block;
    position: absolute;
    right: -40px;
    top: -15px;
  }
  .langs-item {
    margin-bottom: 1px;
  }
  .langs-link {
    color: #B6B6B6;
    font-family: inherit;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 500;
    line-height: 135%;
    /* 21.6px */
    text-transform: uppercase;
    text-decoration: none;
  }
  .langs-link:hover {
    color: #262626;
  }

  .mail-link {
    display: block;
    position: absolute;
    right: -40px;
    bottom: 0px;
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
    color: #262626;
    font-family: inherit;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 135%;
    /* 21.6px */
  }
  .mail-link:hover {
    text-decoration: underline;
  }
}